.clickable-div{cursor:pointer}.com-box .com-title{font-size:30px;color:#8A8C92;margin:0 0 0 30px}.upload-box{width:690px;margin:30px auto;background:#ffffff;border-radius:10px;padding:20px 0}.upload-box h2{margin:0 0 0 30px;color:#2F323A;font-size:30px}.upload-box .img-box{height:340px;margin-top:27px;margin-bottom:16px;overflow:hidden}.upload-box .img-box>div{height:102%;width:100%;position:relative;top:-2px;background:#fff no-repeat center;background-size:contain}.upload-box .img-box img{height:340px;width:100%}.upload-box .tip{margin:0;padding:0 20px;text-align:justify;color:#979797;font-size:26px}.upload-box .upload-btn{width:310px;height:70px;margin:42px auto 0 auto;border-radius:6px;text-align:center;line-height:70px;font-size:30px}.upload-box .upload-true{border:2px solid #A7A9AE;color:#A7A9AE}.upload-box .upload-false{border:2px solid #fd5359;color:#fd5359}



